"use client";

import sampleData from "@/data/sampleListings";

export default function TopRatedPage() {
  // Sort highest rated first
  const sorted = [...sampleData].sort((a, b) => b.avg_rating - a.avg_rating);

  return (
    <div className="p-8 text-white min-h-screen bg-[radial-gradient(circle_at_top_left,#07102a_0%,#03031a_60%)]">
      <h1 className="text-3xl font-bold mb-6">⭐ Top Rated Venues</h1>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {sorted.map((item) => (
          <div
            key={item.id}
            className="bg-[#07162f]/70 border border-zinc-800 rounded-xl p-4"
          >
            <img
              src={item.images[0]}
              alt={item.name}
              className="w-full h-40 object-cover rounded-md mb-3"
            />

            <h2 className="text-xl font-semibold">{item.name}</h2>

            <p className="text-emerald-400 mt-1">
              ⭐ {item.avg_rating.toFixed(1)}/5
            </p>

            <p className="text-sm text-zinc-400 mt-2 line-clamp-2">
              {item.description}
            </p>
          </div>
        ))}
      </div>
    </div>
  );
}
